<template>
	<div>
		<div class="padding-top-20 padding-horizontal-20" v-for="(item,index) in panelData" :key="item.title">
			<div class="page-header">
				<h4 class="page-title">{{item.table}}</h4>
				<div v-if="item.menu" class="page-header-actions" @click="menuCli($event)" style="display: block;">
					<span id="">本周</span>
					<span class="caret"></span>
					<ul class="timeMenu" style="display: none;">
						<li @click="timeCli(index,$event)">本周</li>
						<li @click="timeCli(index,$event)">上周</li>
						<li @click="timeCli(index,$event)">本月</li>
						<li @click="timeCli(index,$event)">上月</li>
					</ul>
				</div>
			</div>
			<div class="page-content">
				<ul class="panel">
					<li class="panel-item" v-for="ite in item.list" :key="ite.name">
						<div class="panel-title">
							{{ite.name}}
						</div>
						<div class="panel-num">
							<a>{{ite.num}}</a>
							<span>个</span>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "Panel",
		data: function() {
			return {

			}
		},
		methods: {
			//时间菜单点击
			menuCli:function(even){
				let aUl = document.getElementsByClassName('timeMenu');
				let ulDis = even.currentTarget.getElementsByTagName('ul')[0].style.display;
				for(let i = 0; i < aUl.length;i++){
					aUl[i].style.display = "none";
				}
				if(ulDis == 'block'){
					even.currentTarget.getElementsByTagName('ul')[0].style.display = 'none';
				}else if(ulDis == 'none'){
					even.currentTarget.getElementsByTagName('ul')[0].style.display = 'block';
				}
				
			},
			//时间选择
			timeCli: function(i,e){
				console.log(i,e.currentTarget.innerHTML)
			},
		},
		props: ['panelData']
	}
</script>

<style>
	/*标题*/
	
	.page-header {
		display: flex;
		justify-content: space-between;
		border-bottom: 0;
		margin: 10px 0 10px;
	}
	
	.page-header-actions {
		position: relative;
		margin-right: 40px;
		align-self: center;
		cursor: pointer;
	}
	/*三角形按钮样式*/
	.caret {
		margin-left: 0;
		border-top: 4px solid;
		display: inline-block;
		width: 0;
		height: 0;
		vertical-align: middle;
		border-right: 4px solid transparent;
		border-left: 4px solid transparent;
	}
	/*时间菜单*/
	.page-header-actions ul {
		position: absolute;
		top: 30px;
		right: 0;
		min-width: 160px;
		padding: 5px 0;
		margin: 2px 0 0;
		font-size: 14px;
		text-align: left;
		list-style: none;
		background-color: #fff;
		border: 1px solid rgba(0, 0, 0, .15);
		border-radius: 4px;
		display: none;
	}
	
	.page-header-actions ul li {
		padding: 8px 15px;
		height: 36px;
	}
	
	.page-content {
		margin-bottom: 20px;
		background-color: #fff;
		border: 1px solid #CCCCCC;
		border-radius: 4px;
		box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
		padding: 15px;
	}
	/*主要内容*/
	.panel {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	
	.panel-item {
		padding: 15px;
	}
	
	.panel-title {
		color: #455a64;
	}
	
	.panel-num {
		height: 44px;
		line-height: 44px;
	}
	
	.panel-num a {
		font-size: 28px;
		color: #f96868;
	}
	
	.panel-num span {
		font-size: 18px;
		color: #f96868;
	}
</style>