import Vue from 'vue'
import Router from 'vue-router'
import Workbench from './views/Workbench.vue'


Vue.use(Router)

export default new Router({
	mode: 'hash',
	base: process.env.BASE_URL,
	routes: [{
		path: '/',
		name: 'home',
		component:  () =>
				import('./views/Home.vue'),
		children: [{
			path: '/workbench',
			component: Workbench,

		}, {
			path: '/customer',
			name: 'customer',

			component: () =>
				import('./views/Customer.vue'),
		}, {
			path: '/constract',
			name: 'constract',

			component: () =>
				import('./views/Constract.vue')
		}, {
			path: '/payment',
			name: 'payment',

			component: () =>
				import('./views/Payment.vue')
		}, {
			path: '/note',
			name: 'note',

			component: () =>
				import('./views/Note.vue')
		}, {
			path: '/product',
			name: 'product',

			component: () =>
				import('./views/Product.vue')
		}, {
			path: '/stratistics',
			name: 'stratistics',

			component: () =>
				import('./views/Stratistics.vue')
		}, {
			path: '/none',
			name: 'none',

			component: () =>
				import('./views/None.vue')
		}, ]
	}, {
		path: '/login',
		component: () =>
				import('./views/Login.vue')
	}]
})