<template>
	<div id="myChart" style="width: 600px;height:400px;"></div>
</template>

<script>
	export default {
		data: function() {
			return {}
		},
		methods: {
			drawLine() { //统计图
				let myChart = this.$echarts.init(document.getElementById('myChart'))
				var option = {
					title: {
						text: '客户分类统计',
						x: 'center'
					},
					tooltip: {
						trigger: 'item',
						formatter: "{a} <br/>{b} : {c} ({d}%)"
					},
					series: [{
						name: '访问来源',
						type: 'pie',
						radius: '55%',
						center: ['50%', '60%'],
						data: [{
								value: 335,
								name: '直接访问'
							},
							{
								value: 310,
								name: '邮件营销'
							},
							{
								value: 234,
								name: '联盟广告'
							},
							{
								value: 135,
								name: '视频广告'
							},
							{
								value: 1548,
								name: '搜索引擎'
							}
						],
						itemStyle: {
							emphasis: {
								shadowBlur: 10,
								shadowOffsetX: 0,
								shadowColor: 'rgba(0, 0, 0, 0.5)'
							}
						}
					}]
				};
				myChart.setOption(option);
			}
		},
		mounted() {
			this.drawLine();
		},
	}
</script>

