<template>
	<div class="home">
		<!--条形数据板-->
		<Panel :panelData="panelData"></Panel>
		<!--表格-->
		<div class="padding-top-20 padding-horizontal-20">
			<div class="page-header">
				<h4 class="page-title">提醒/到期合同/联系任务</h4>
			</div>
			<div class="page-content">
				<ul class="nav-tabs">
					<li :class="tabIndex == 0 ? 'active':''" @click="tabCli(0)">未完成任务提醒</li>
					<li :class="tabIndex == 1 ? 'active':''" @click="tabCli(1)">本月到期合同</li>
					<li :class="tabIndex == 2 ? 'active':''" @click="tabCli(2)">联系任务</li>
				</ul>
				<div class="tab-content padding-top-20">
					<Table :tableData="tableData" :newTrData="newTrData" @tableFun="tableFun"></Table>
				</div>
			</div>
		</div>
		<!--统计图数据-->
		<div class="padding-top-20 padding-horizontal-20 page-bottom">
			<div class="padding-top-20 padding-horizontal-20 page-bottom-left">
				<div class="page-header">
					<h4 class="page-title">客户分类统计</h4>
					<div class="page-header-actions">
						<span>
							按员工查看
						</span>
						<span class="caret"></span>
						<ul>
							<li>本周</li>
							<li>上周</li>
							<li>本月</li>
							<li>上月</li>
						</ul>
					</div>
					<div class="page-header-actions">
						<span id="">
							按饼图显示
						</span>
						<span class="caret"></span>
						<ul>
							<li>按饼图显示</li>
							<li>按销售漏斗</li>
						</ul>
					</div>
				</div>
				<div class="page-main">
					<PieGraph></PieGraph>
				</div>
			</div>
			<div class="padding-top-20 padding-horizontal-20 page-bottom-right">
				<div class="page-header">
					<h4 class="page-title">销售漏斗</h4>
					<div class="page-header-actions">
						<span id="">
							按员工查看
						</span>
						<span class="caret"></span>
						<ul>
							<li>本周</li>
							<li>上周</li>
							<li>本月</li>
							<li>上月</li>
						</ul>
					</div>
					<div class="page-header-actions">
						<span id="">
							按签单时间统计
						</span>
						<span class="caret"></span>
						<ul>
							<li>按签单时间统计</li>
							<li>按创建时间统计</li>
						</ul>
					</div>
					<div class="page-header-actions">
						<span id="">本周</span>
						<span class="caret"></span>
						<ul>
							<li>本周</li>
							<li>上周</li>
							<li>本月</li>
							<li>上月</li>
						</ul>
					</div>
				</div>
				<div class="page-main">
					<FunnelPlot></FunnelPlot>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Panel from '../components/Panel'
	import Table from '../components/Table'
	import PieGraph from '../components/PieGraph'
	import FunnelPlot from '../components/FunnelPlot'
	export default {
		name: 'workbench',
		components: {
			Panel,
			Table,
			PieGraph,
			FunnelPlot
		},
		data: function() {
			return {
				tabIndex: 0, //选项卡点击下标
				newTrData:'', // 新分页数据
				panelData: [{ //数据版数据
					table: "客户统计",
					list: [{
						'name': "客户总数量",
						'num': 0
					}, {
						'name': "所有未联系客户",
						'num': 0
					}, {
						'name': "逾期待联系客户",
						'num': 0
					}, {
						'name': "今日待联系客户",
						'num': 0
					}, {
						'name': "今日已联系客户",
						'num': 0
					}, {
						'name': "今日新创建和分配客户",
						'num': 0
					}],
					menu: false
				}, {
					table: "本周款项记录",
					list: [{
						'name': "成交合同数",
						'num': 0
					}, {
						'name': "成交合同金额",
						'num': 0
					}, {
						'name': "回款金额",
						'num': 0
					}, {
						'name': "开票金额",
						'num': 0
					}],
					menu: true
				}, {
					table: "本周新增数据统计",
					list: [{
						'name': "新创建客户",
						'num': 0
					}, {
						'name': "新创建销售机会",
						'num': 0
					}, {
						'name': "联系客户数",
						'num': 0
					}, {
						'name': "新成交客户",
						'num': 0
					}],
					menu: true
				}],
				// 表格数据
				tableData: { 
					thData: [{
							name: '编号',
							key: 'id'
						},
						{
							name: '任务内容',
							key: 'address'
						},
						{
							name: '任务负责人',
							key: 'name'
						}, {
							name: '开始时间',
							key: 'date'
						}
					],
					trData: [{
						id: 0,
						date: '2016-05-03',
						name: '王',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 1,
						date: '2016-05-02',
						name: '小',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 2,
						date: '2016-05-04',
						name: '虎',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 3,
						date: '2016-05-01',
						name: '王小虎',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 4,
						date: '2016-05-08',
						name: '王小虎',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 5,
						date: '2016-05-06',
						name: '王小虎',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 6,
						date: '2016-05-07',
						name: '王小虎',
						address: '上海市普陀区金沙江路 1518 弄'
					}]
				},
			}
		},
		methods: {
			tabCli: function(index) { //选项卡点击切换
				this.tabIndex = index;
			},
			tableFun: function(data){ //点击翻页时获取数据
				this.newTrData = data;
			}
		},
		watch: {
			tabIndex: function(newTabIndex) {  //选项卡改变时.重新渲染数据
				if (newTabIndex == 0) {
					this.tableData.thData = [{
							name: '编号',
							key: 'id'
						},
						{
							name: '任务内容',
							key: 'address'
						},
						{
							name: '任务负责人',
							key: 'name'
						}, {
							name: '开始时间',
							key: 'date'
						}
					]

				} else if (newTabIndex == 1) {
					this.tableData.thData = [{
							name: '编号',
							key: 'id'
						},
						{
							name: '合同编号',
							key: 'address'
						},
						{
							name: '所属客户',
							key: 'name'
						}, {
							name: '负责人',
							key: 'date'
						}, {
							name: '合同金额',
							key: 'date'
						}, {
							name: '负合同签约日期',
							key: 'date'
						}, {
							name: '合同开始日期',
							key: 'date'
						}, {
							name: '合同结束日期',
							key: 'date'
						}
					]
				} else if (newTabIndex == 2) {
					this.tableData.thData = [{
							name: '编号',
							key: 'id'
						},
						{
							name: '任务名称',
							key: 'address'
						},
						{
							name: '任务类型',
							key: 'name'
						}, {
							name: '开始时间',
							key: 'date'
						}, {
							name: '总客户数',
							key: 'date'
						}, {
							name: '联系数',
							key: 'date'
						}, {
							name: '联系进度',
							key: 'date'
						}
					]
				} else {
					this.tableData = {}
				}

			}
			//				this.tableData.th=['编号', '任务内容', '任务负责人']
		},
		created() {
			this.newTrData = this.tableData.trData.slice(0, 3)
		},
	}
</script>
<style scoped>
	.nav-tabs {
		display: flex;
		border-bottom: solid 1px #ccc;
	}

	.nav-tabs li {
		padding: 10px 20px;
		cursor: pointer;
	}

	.nav-tabs .active {
		color: #fff;
		background-color: #3f51b5;
		border-color: transparent;
		border-bottom-color: #3f51b5;
		border-radius: 5px 5px 0 0;
	}

	.page-bottom {
		display: flex;
	}

	.page-bottom-left,
	.page-bottom-right {
		width: 50%;
	}

	.page-main {
		margin: 40px 0;
	}
</style>
