<template>
	<div>
		<!--  :data数据源  stripe实现斑马线效果 @selection-change:当选择项发生改变时触发此事件  @row-click 点击行时触发事件 -->
		<el-table :data="newTrData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
		 tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" @row-click="rowClisk" stripe="stripe"
		 border="border">
			<!-- type="selection" 实现多选 -->
			<el-table-column type="selection" width="55"></el-table-column>
			<!-- 表格 -->
			<!-- 默认内容过多会多行显示.设置show-overflow-tooltip为单行显示.内容过多时会在 hover 时以 tooltip 的形式显示出来。 -->
			<el-table-column v-for="(item,index) in tableData.thData" :key="index" :label="item.name" show-overflow-tooltip
			 align="center" :width="index == 0? '80':''">
				<template slot-scope="scope">{{ scope.row[item.key] }}</template>
			</el-table-column>
		</el-table>
		<!-- total:总条数 hide-onsingle-page:只有一页时是否隐藏 page-size：每页的数据量 @current-change：当页数改变时触发 -->
		<el-pagination class="mg-tb-20" layout=" prev, pager, next,total" :total="tableData.trData.length"
		 :hide-on-single-page="newTrData.length<1" :page-size="pageSize" @current-change="handleCurrentChange"></el-pagination>
	</div>
</template>

<script>
	export default {
		name: 'Table',
		data() {
			return {
				pageSize: 3, //每页的数据数目
			}
		},
		props: ['search', 'tableData','newTrData'],
		methods: {
			rowClisk(row) { //鼠标点击行事件
				this.$emit('showDetails', row) //把当前选中的行数据传递给父组件
			},
			handleSelectionChange(val) { //当选择项发生改变时会触发该事件
				console.log(val)
				// 传出
			},
			handleCurrentChange(val) { //点击翻页器的时候触发
				console.log(`当前页: ${val}`);
				let tableData = this.tableData.trData.slice((val - 1) * this.pageSize, val * this.pageSize)
				this.$emit('tableFun',tableData)  
			},
		},
	}
</script>
