<template>
	<div id="myChart2" style="width: 600px;height:400px;"></div>
</template>

<script>
	export default {
		data: function() {
			return {}
		},
		methods: {
			drawLine() { //统计图
				let myChart2 = this.$echarts.init(document.getElementById('myChart2'))

				var option2 = {
					tooltip: {
						trigger: 'item',
						formatter: "{a} <br/>{b} : {c}%"
					},
					toolbox: {
						feature: {
							dataView: {
								readOnly: false
							},
							restore: {},
							saveAsImage: {}
						}
					},
					legend: {
						data: ['需求提案', '投标', '商务谈判', '赢单', '订单']
					},
					calculable: true,
					series: [{
						name: '漏斗图',
						type: 'funnel',
						left: '10%',
						top: 60,
						//x2: 80,
						bottom: 60,
						width: '80%',
						// height: {totalHeight} - y - y2,
						min: 0,
						max: 100,
						minSize: '0%',
						maxSize: '100%',
						sort: 'descending',
						gap: 2,
						label: {
							show: true,
							position: 'inside'
						},
						labelLine: {
							length: 10,
							lineStyle: {
								width: 1,
								type: 'solid'
							}
						},
						itemStyle: {
							borderColor: '#fff',
							borderWidth: 1
						},
						emphasis: {
							label: {
								fontSize: 20
							}
						},
						data: [{
								value: 60,
								name: '需求提案'
							},
							{
								value: 40,
								name: '投标'
							},
							{
								value: 20,
								name: '商务谈判'
							},
							{
								value: 80,
								name: '赢单'
							},
							{
								value: 100,
								name: '展现'
							}
						]
					}]
				};
				myChart2.setOption(option2);
			}
		},
		mounted() {
			this.drawLine();
		},
	}
</script>

